import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import InboxIcon from "@material-ui/icons/Inbox";
import DraftsIcon from "@material-ui/icons/Drafts";
import { Route, NavLink } from "react-router-dom";
import TextField from "@material-ui/core/TextField";
import System from './system';
import './admin.scss';

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    maxWidth: 260,
    boxShadow: "1px 0 10px #e8e8e8",
    backgroundColor: "#353535"
  },
}));

export default function SelectedListItem() {
  const classes = useStyles();
  const [selectedIndex, setSelectedIndex] = React.useState(0);

  const handleListItemClick = (event, index) => {
    setSelectedIndex(index);
  };

  return (
    <div Style="display: flex; height: 100%; background-color: #eee">
    <div className={classes.root}>
      <List component="nav" aria-label="main mailbox folders">
        <NavLink to="/admin/system" className="admMenu">
        <ListItem
          button
          selected={selectedIndex === 0}
          onClick={(event) => handleListItemClick(event, 0)}
        >
          <ListItemIcon>
            <InboxIcon />
          </ListItemIcon>
          <ListItemText primary="시스템 운영 관리" />
        </ListItem>
        </NavLink>

      <NavLink to="/admin/coupon" className="admMenu">
        <ListItem
          button
          selected={selectedIndex === 1}
          onClick={(event) => handleListItemClick(event, 1)}
        >
          <ListItemIcon>
            <DraftsIcon />
          </ListItemIcon>
          <ListItemText primary="쿠폰 콘텐츠 서비스 관리" />
        </ListItem>
        </NavLink>

<NavLink to="/admin/coupon" className="admMenu">
        <ListItem
          button
          selected={selectedIndex === 2}
          onClick={(event) => handleListItemClick(event, 2)}
        >
          <ListItemIcon>
            <DraftsIcon />
          </ListItemIcon>
          <ListItemText primary="인공지능" />
        </ListItem>
        </NavLink>
      </List>
      <Divider />
      

      
    </div>
    
      <Route path="/admin/system" component={System} />
    </div>
  );
}
