import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import ListAltIcon from "@material-ui/icons/ListAlt";
import WidgetsIcon from "@material-ui/icons/Widgets";
import FeaturedPlayListIcon from "@material-ui/icons/FeaturedPlayList";
import Box from "@material-ui/core/Box";
import Campaigns from "../components/campaigns";
import Adsets from "../components/adsets";
import Ads from "../components/ads";
import "./center.scss";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: 1600,
    margin: "0 auto",
    padding: 30,
    flexGrow: 1,
  },
}));

export default function Center() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Tabs value={value} onChange={handleChange}>
          <Tab label="캠페인" icon={<ListAltIcon />} {...a11yProps(0)} />
          <Tab label="광고세트" icon={<WidgetsIcon />} {...a11yProps(1)} />
          <Tab label="광고" icon={<FeaturedPlayListIcon />} {...a11yProps(2)} />
        </Tabs>
      </AppBar>

      <TextField
        id="standard-search"
        label="검색어를 입력하세요"
        type="search"
        Style="margin: 0 0 30px 0; float: right"
      />

      <TabPanel value={value} index={0}>
        <Campaigns />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <Adsets />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <Ads />
      </TabPanel>
    </div>
  );
}
